* {
    margin: 0;
    padding: 0;
}

:root {
    --text-main: #080025;
    --royal-gold: #D4AF37;
    --Metallic-Gold: #B8860B;
    --Rich-Gold: #FFD700;
    --Antique-Gold: #C28840;
    --Luxury-Matte-Gold: #C9B037;
    --bg: #080025;
}

/* ---------- */
/* --Globle-- */
/* ---------- */

.page-container {
    margin-top: 75px;
}

.react-link {
    text-decoration: none;
    color: var(--text-main);
}

/* --btn-- */

.btn-flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn {
    min-width: 90px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    outline: none;
    border: none;
    font-weight: 400;
    font-size: 15px;
    border-radius: 3px;
    background-color: var(--bg);
    color: white;
}

.light-btn {
    background-color: rgb(186, 186, 186);
    color: var(--text-main);
}

.btn .royal-btn {
    background: var(--royal-gold);
    color: white;
}


/* ---------- */
/* --Nav Bar-- */
/* ---------- */

.NavBar {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: fixed;
    top: 0px;
    z-index: 100;
}

.NavBar .NavBar-nav {
    width: 100%;
    height: 75px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    box-shadow: gray 1px 0 10px;
}

.NavBar .NavBar-nav .NavBar-nav-box {
    width: 80%;
    height: 100%;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.NavBar .NavBar-nav .NavBar-nav-box .logo img {
    width: 10rem;
}

.NavBar .NavBar-nav .menu-items-group {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    font-weight: 500;
    font-size: 20px;
}

.NavBar .NavBar-nav .menu-items-group .menu-items {
    display: flex;
    gap: 2rem;
}

.NavBar .NavBar-nav .menu-items-group .menu-items .react-link:hover {
    text-shadow: #fc0 1px 0 10px;
}

/* Desktop Dropdown */
.dropdown {
    position: relative;
}

.dropdown-content {
    min-width: 150px;
    display: none;
    position: absolute;
    top: 100%;
    background-color: white;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    z-index: 10;
    padding: 10px;
}

.dropdown:hover .dropdown-content {
    display: flex;
    justify-content: center;
    align-items: baseline;
    gap: 1rem;
    flex-direction: column;

}

.NavBar .NavBar-nav .menu-items-group .menu-language-btn {
    width: 90px;
    height: 35px;
    background-color: var(--bg);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
    cursor: pointer;
    font-weight: 500;
    /* font-size: 20px; */
}

.NavBar .NavBar-nav .menu-items-group .menu-language-btn select {
    height: 100%;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    color: white;
}

.NavBar .NavBar-nav .menu-items-group .menu-language-btn select option {
    color: white;
    background-color: #080025;
    width: 100px;
    outline: none;
    border: none;
}

.menu-toggle {
    display: none;
    cursor: pointer;
}

/* --- */
/* .NavBar .responsive-menu {
    width: 200px;
    position: absolute;
    top: 75px;
    left: 0;
} */
.NavBar .responsive-menu .menu-items {
    display: flex;
    align-items: baseline;
    justify-content: center;
    flex-direction: column;
    gap: 1rem;
}

.NavBar .responsive-menu .menu-items .mobile-language {
    padding: 6px;
    background-color: var(--text-main);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
    font-weight: bold;
    font-size: 1rem;
}

.NavBar .responsive-menu .menu-items .mobile-language select {
    border: none;
    outline: none;
    background: transparent;
    color: white;
}


/* Mobile */
@media (max-width: 768px) {
    .NavBar .NavBar-nav {
        position: fixed;
        top: 0px;
    }

    .NavBar .NavBar-nav .NavBar-nav-box {
        width: 90%;
    }

    .NavBar .NavBar-nav .NavBar-nav-box .logo img {
        width: 7rem;
    }

    .NavBar .NavBar-nav .menu-items-group .menu-items {
        display: none;
    }

    .hide-on-mobile {
        display: none !important;
    }

    .mobile-only {
        display: block;
    }

    .responsive-menu {
        display: flex;
        flex-direction: column;
        background: white;
        width: 100%;
        padding: 1rem 2rem;
        position: absolute;
        top: 75px;
        left: 0;
        z-index: 999;
    }

    .menu-language-btn.mobile-language {
        margin-top: 1rem;
    }
}

@media (min-width: 769px) {
    .mobile-only {
        display: none !important;
    }
}


/* ---------- */
/* --Home-- */
/* ---------- */
.App .home-hero {
    width: 100%;
    height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url('./Components/Assets/main-background.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    backdrop-filter: blur(0.5);
}

.App .home-hero .home-hero-crousole {
