    height: 100%;
}

.home-hero-crousole-box {
    width: 100%;
    height: 100%;
    position: relative;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.App .home-hero .home-hero-crousole .crousole-bg {
    width: 100%;
    height: 100%;
    background-color: rgba(1, 1, 1, 0.633);
    position: absolute;
    top: 0px;
    z-index: 1;
}

.hero-crousole {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 2rem;
    color: white;
    position: absolute;
    top: 0px;
    z-index: 2;

}

.hero-crousole h1 {
    width: 50%;
    color: white;
    font-size: 4rem;
    text-align: center;
}

.hero-crousole p {
    width: 50%;
    text-align: center;
    font-size: 1.3rem;
}

.hero-crousole .btn {
    width: 8rem;
    font-weight: bold;
}

.App .home-section {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5rem;
    gap: 2rem;
}

.App .home-section h2 {
    font-size: 3rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: var(--text-main);
}

.App .home-section .home-sub-section {
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 2rem;
}

.home-section .home-sub-section .details {
    width: 400px;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    gap: 1rem;
    color: var(--text-main);
}

.home-section .home-sub-section .image img {
    width: 450px;
    box-shadow: 0 2px 5px rgb(71, 71, 71);
    border-radius: 3rem;
}

/* --- */

.home-offer {
    flex-direction: column !important;
}

.home-offer .home-sub-section {
    margin-top: 2rem;
}

.home-offer .offer-card {
    width: 200px;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 1rem;
    font-size: 21px;
    font-weight: 500;
    border-radius: 3px;
    box-shadow: gray 1px 0 10px;
}

.home-offer .offer-card .icon {
    font-size: 3rem;
}

.home-offer .offer-card:hover {
    background-color: var(--Luxury-Matte-Gold);
    color: white;
}

/* ----- */
.home-section .home-choose .details {
    width: 50%;

    span {
        font-size: 1.2rem;
        font-weight: bold;
        margin-right: 0.5rem;
    }
}

/* ----- */
.collection-box {
    flex-direction: column;
    gap: 5px;
}

.collection-box .collection-hero {
    width: 90%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 3rem;
}

.collection-box .collection-hero .collection-controllers {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;
    margin-bottom: 3rem;
}

.collection-box .collection-hero .collection-controllers .btn-flex {
    gap: 2rem;
}

.collection-box .collection-hero .collection-controllers .btn-flex .active {
    background-color: var(--text-main);
    color: white;
}

.collection-box .collection-hero .crousole-btn-group .btn {
    width: 40px !important;
    height: 40px !important;
    min-width: 40px !important;
    max-width: 40px !important;
    border-radius: 50%;
    background-color: rgb(255, 128, 0);
    color: white;
    padding: 0 !important;
}


.collection-crousole-container {
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
}

.home-section .collection-crousole {
    width: 80%;
    display: flex;
    align-items: center;
    gap: 3rem;
    scroll-behavior: smooth;
}

.home-section .collection-crousole .card {
    width: 15pc;
    height: 20pc;
    background: #fff;
    border-radius: 3px;
    box-shadow: var(--text-main) 1px 0 10px;
    transition: transform 0.3s ease-in-out;
}

.home-section .collection-crousole .card:hover {
    transform: translateY(-5px);
}


.collection-crousole .card .image {
    width: 15pc;
    height: 15pc;
}

.collection-crousole .card .image img {
    width: 100%;
    height: 15pc;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}

.collection-crousole .card .details {
    height: 5pc;
    padding: 0 0 0 1rem;
    display: flex;
    align-items: center;
    justify-content: baseline;
    background-color: var(--bg);
    color: white;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
}

/* Mobile */
@media (max-width: 768px) {
    .App .home-hero {
        height: 50vh;
    }

    .hero-crousole h1 {
        width: 90%;
        color: white;
        font-size: 2rem;
        text-align: center;
    }

    .hero-crousole p {
        width: 90%;
        text-align: center;
        font-size: 1rem;
    }

    .hero-crousole .btn {
        width: 8rem;
        font-weight: bold;
    }

    .App .home-section .home-sub-section {
        justify-content: center;
    }

    .home-section .home-sub-section .image img {
        width: 90%;
        border-radius: 1rem;
    }

    .App .home-section h2 {
        font-size: 2rem;
    }

    .home-section .home-sub-section .details {
        width: 100%;
    }
}

/* ------- */
/* About */
/* ------- */
.about .about-hero {
    width: 100%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: url('./Components/Assets/home-about.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    top: 0px;
}

.about .about-hero .background-filter {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    z-index: 9;
    background-color: #08002596;
}

.about .about-hero .details {
    position: absolute;
    top: 0px;
    z-index: 10;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.about .about-hero .details .header{
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.about .about-hero .details .header h2 {
    font-size: 3rem;
    text-align: center;
    color: white;
}

.about .about-hero .details .header p{
    font-size: 1.5rem;
    color: white;
    text-align: center;
}

.about .about-section {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
    flex-wrap: wrap;
}

.about .about-section .about-sub-section {
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap-reverse;
    margin-top: 5rem;
    gap: 2rem;
}

.about .about-section .about-sub-section h2 {
    color: var(--text-main);
    font-size: 3rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.about .about-section .about-sub-section .about-details {
    width: 40%;
}

.about .about-section .about-sub-section .image {
    width: 40%;
}

.about .about-section .about-sub-section .image img {
    width: 100%;
    box-shadow: 0 2px 5px rgb(71, 71, 71);
    border-radius: 3rem;
}

.about .future .about-details {
    box-shadow: 0 2px 5px rgb(71, 71, 71);
    padding: 1rem;
    border-radius: 0.4rem;
}

.about .future .about-details .icon-box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0.6rem;
}

.about .future .about-details .icon-box .icon {
    font-size: 3rem;
}

.about .future .about-details h2 {
    font-size: 1.5rem;
}

.about-section .about-sub-section .container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 2rem;
}

.about-section .about-sub-section .container .card-group {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 3rem;
    flex-wrap: wrap;
}

.about-section .about-sub-section .container .card {
    width: 300px;
    display: flex;
    flex-direction: column;
    background-color: var(--text-main);
    border-radius: 4px;
    /* normal state - 4px all around */
    overflow: hidden;
    transition: all 0.4s ease-in-out;
    position: relative;
    box-shadow: 0 2px 5px rgb(71, 71, 71);
}

.about-section .about-sub-section .container .card .image {
    width: 100%;
    border-radius: 4px;
    /* normal */
    transition: border-radius 0.4s ease-in-out;
}

.about-section .about-sub-section .container .card .image img {
    width: 100%;
    display: block;
    border-radius: inherit;
    transition: border-radius 0.4s ease-in-out;
}

/* details box initially hidden (slide + fold down) */
.about-section .about-sub-section .container .card .details {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    /* background: rgba(0, 0, 0, 0.6); */
    background-color: var(--text-main);
    color: white;
    height: 50px;
    padding-left: 0.7rem;
    display: flex;
    /* align-items: center; */
    justify-content: center;
    flex-direction: column;

    /* lifafe (folded) effect */
    transform: translateY(100%) scaleY(0);
    transform-origin: bottom center;
    transition: transform 0.5s cubic-bezier(0.25, 1.25, 0.5, 1);

    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}

/* hover effect */
.about-section .about-sub-section .container .card:hover {
    border-radius: 4px 3rem 4px 4px;
    /* top-right = 3rem, rest = 4px */
}

/* image bottom radius also 3rem on hover */
.about-section .about-sub-section .container .card:hover .image img {
    border-bottom-left-radius: 3rem;
    border-bottom-right-radius: 3rem;
}

/* slide + unfold details on hover */
.about-section .about-sub-section .container .card:hover .details {
    transform: translateY(0) scaleY(1);
}


/* Mobile */
@media (max-width: 768px) {
    .about .about-hero {
        height: 50vh;
    }

    .about .about-hero .details .header h2 {
        width: 90%;
        color: white;
        font-size: 2rem;
    }

    .about .about-hero .details .header p{
        width: 90%;
        font-size: 1rem;
    }

    .about .about-section .about-sub-section h2 {
        font-size: 2rem;
    }

    .about .about-section .about-sub-section .image img {
        width: 90%;
        border-radius: 1rem;
    }

    .about .about-section .about-sub-section .about-details {
        width: 100%;
    }

    .about .about-section .about-sub-section .image {
        width: 100%;
    }
}

